
<!
<template>
    <el-button type="text" @click="open3"></el-button>
</template>


<script>
    export default {
        name: "JuniorCheck",
        methods: {
            open3() {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                    inputErrorMessage: ''
                }).then(({ value }) => {
                    this.$message({
                        type: 'success',
                        message: ': ' + value
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
        }
    }

</script>

<style scoped>

</style>



